.Dashboard {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    padding-left: 50px;
    padding-top: 60px;
    background-color: white;
    width: 100%;
}

.dashboardHeaderText {
    font-weight: bold;
    font-size: 2rem;
}

.dropdownText {
    font-size: large;
    font-weight: 600;
}

.timeZone {
    display: flex;
    justify-content: flex-start;
    align-items: center;
}